html {
  height: 100%; }

body {
  width: 100%;
  min-width: 1025px;
  background-color: #ffffff;
  margin: 0 auto;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 155%;
  font-style: normal;
  color: #242424;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  position: relative; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%); }

@media (max-width: 1024px) {
  body {
    min-width: 768px;
    font-size: 16px; } }

@media (max-width: 767px) {
  body {
    min-width: 320px;
    font-size: 14px; } }

@font-face {
  font-family: "Roboto";
  font-weight: normal;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Fontawesome";
  font-weight: normal;
  src: url("../fonts/fontawesome-webfont.woff2") format("woff2"), url("../fonts/fontawesome-webfont.woff") format("woff");
  font-display: swap; }

.button {
  text-align: center;
  display: block;
  box-sizing: border-box;
  background-color: #ffffff;
  border: 1px solid #dde3ed;
  border-radius: 24px;
  margin: 0 auto;
  cursor: pointer; }
  .button:hover {
    border: 1px solid #2866ae;
    background-color: #2866ae;
    color: #ffffff; }

.side-menu {
  min-width: 260px;
  max-width: 260px;
  height: max-content; }
  .side-menu__list, .side-menu__sublist {
    list-style: none;
    margin: 0;
    padding: 0; }
  .side-menu__list {
    box-sizing: border-box;
    height: max-content;
    border: 1px solid #dde3ed;
    border-radius: 10px;
    padding: 0 20px; }
    .side-menu__list--hide {
      display: none; }
  .side-menu__sublist {
    display: none;
    padding: 0 0 0 16px; }
  .side-menu__item {
    box-sizing: border-box;
    padding: 16px 0;
    border-bottom: 1px solid #dde3ed; }
    .side-menu__item input:checked ~ .side-menu__sublist {
      display: block; }
    .side-menu__item input:checked ~ .side-menu__item-title::after {
      content: "\f106"; }
    .side-menu__item:last-child {
      border: none; }
    .side-menu__item a:hover,
    .side-menu__item span:hover {
      color: #2866ae; }
  .side-menu__subitem {
    box-sizing: border-box;
    padding: 16px 0 0; }
  .side-menu__item-title {
    cursor: pointer;
    text-decoration: none;
    color: #242424;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
    .side-menu__item-title::after {
      display: block;
      content: "\f105";
      font-family: "Fontawesome";
      font-size: 1.3em;
      margin: 0 0 0 8px; }
  .side-menu__button {
    display: none; }

@media (max-width: 1024px) {
  .side-menu {
    min-width: 220px;
    max-width: 220px; }
    .side-menu__list {
      padding: 0 12px; }
    .side-menu__item-title {
      font-size: 0.88em; } }

@media (max-width: 767px) {
  .side-menu {
    max-width: unset;
    margin-bottom: 32px; }
    .side-menu__button {
      display: block;
      padding: 12px;
      width: 280px;
      margin-bottom: 16px; } }

.page-main {
  width: 100%;
