.side-menu {
  min-width: 260px;
  max-width: 260px;
  height: max-content;

  &__list,
  &__sublist {
    @include list;
  }

  &__list {
    box-sizing: border-box;
    height: max-content;
    border: 1px solid $grey-border;
    border-radius: 10px;
    padding: 0 20px;

    &--hide {
      display: none;
    }
  }

  &__sublist {
    display: none;
    padding: 0 0 0 16px;
  }

  &__item {
    box-sizing: border-box;
    padding: 16px 0;
    border-bottom: 1px solid $grey-border;

    input:checked ~ .side-menu__sublist {
      display: block;
    }

    input:checked ~ .side-menu__item-title {
      &::after {
        content: "\f106";
      }
    }

    &:last-child {
      border: none;
    }

    & a:hover,
    & span:hover {
      color: $blue;
    }
  }

  &__subitem {
    box-sizing: border-box;
    padding: 16px 0 0;
  }

  &__item-title {
    cursor: pointer;
    text-decoration: none;
    color: $black;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    &::after {
      display: block;
      content: "\f105";
      font-family: $Fontawesome;
      font-size: 1.3em;
      margin: 0 0 0 8px;
    }
  }

  &__button {
    display: none;
  }
}

@media (max-width: $tablet-max-width) {
  .side-menu {
    min-width: 220px;
    max-width: 220px;

    &__list {
      padding: 0 12px;
    }

    &__item-title {
      font-size: 0.88em;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .side-menu {
    max-width: unset;
    margin-bottom: 32px;

    &__button {
      display: block;
      padding: 12px;
      width: 280px;
      margin-bottom: 16px;
    }
  }
}
