.content {
  width: 100%;
  max-width: 870px;
  margin: 0 0 0 48px;

  h1 {
    @include title-h1;
  }

  & > p {
    margin: 32px 0 0;
  }

  &__list {
    @include list;
    margin: 40px 0 0;

    &--service {
      display: flex;
      justify-content: space-between;

      .content__item {
        display: block;
        text-align: center;
        width: 49%;
        margin: 0;

        a {
          text-decoration: none;
          color: $black;

          &:hover {
            color: $blue;
          }
        }

        img {
          width: 100%;
          height: auto;
        }

        h2 {
          @include title-h2;
          margin: 16px 0 0;
        }
      }
    }

    &--repair {
      .content__item {
        display: flex;
        justify-content: space-between;
        margin: 0 0 60px;
        align-items: center;

        img {
          width: 100%;
          min-width: 200px;
          max-width: 339px;
          height: auto;
        }

        &:nth-child(2n) {
          flex-direction: row-reverse;

          .content__item-description {
            margin: 0 24px 0 0;

            h2 {
              @include title-h1;
            }
          }
        }
      }

      .content__item-description {
        padding: 0;
        background-color: unset;
        min-width: 400px;
        width: 100%;
        max-width: 500px;
        margin: 0 0 0 24px;

        h2 {
          @include title-h1;
        }
      }
    }
  }

  &__item {
    display: flex;
    margin: 0 0 32px;

    img {
      width: 180px;
      height: 195px;
    }
  }

  &__item-description {
    box-sizing: border-box;
    padding: 20px 20px 20px 40px;
    background-color: $grey-background;

    h2 {
      font-size: 1.2em;
      margin: 0;
    }

    p {
      margin: 16px 0 0;
    }
  }

  &__schedule-list {
    @include list;
    margin: 40px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  &__schedule-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 42%;
    box-sizing: border-box;
    padding: 15px;
    background-color: $blue;
    color: $white;
    margin: 0 0 32px;

    &:nth-child(2n) {
      background-color: $blue-background;
      margin: 0 0 32px 32px;
    }
  }

  &__schedule-item-date {
    @include title-h2;
    width: 100px;
    text-align: center;
    border-bottom: 1px solid $white;
  }

  &__schedule-item-time {
    font-weight: 600;
    padding: 0 0 0 16px;
    border-bottom: 1px solid $white;
  }

  &__schedule-item-title {
    padding: 0 0 0 16px;
  }

  &__schedule-item-button {
    width: 200px;
    margin: 48px 0 0 auto;
    padding: 14px;
    border-radius: 8px;
    color: $blue;
    text-decoration: none;
    border: unset;

    &:hover {
      color: $blue;
      background-color: $white;
      border: unset;
    }
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    margin: 0 0 0 32px;

    & > p {
      margin: 24px 0 0;
    }

    &__list {
      margin: 32px 0 0;

      &--service {
        flex-direction: column;

        .content__item {
          width: 100%;
          max-width: 579px;
          margin: 0 auto 32px;

          h2 {
            @include title-h2;
            margin: 8px 0 0;
          }
        }
      }

      &--repair {
        .content__item {
          margin: 0 0 40px;

          img {
            min-width: 130px;
          }
        }

        .content__item-description {
          padding: 0;
          background-color: unset;
          min-width: 280px;
          width: 100%;
          max-width: 500px;
          margin: 0 0 0 24px;

          h2 {
            @include title-h1;
          }
        }
      }
    }

    &__item {
      display: flex;
      margin: 0 0 24px;
    }

    &__item-description {
      box-sizing: border-box;
      padding: 20px;
      background-color: $grey-background;

      h2 {
        font-size: 1em;
        margin: 0;
      }

      p {
        margin: 8px 0 0;
        font-size: 0.88em;
      }
    }

    &__schedule-list {
      margin: 32px 0 0;
      justify-content: space-between;
    }

    &__schedule-item {
      width: 47%;
      margin: 0 0 24px;

      &:nth-child(2n) {
        margin: 0 0 24px 24px;
      }
    }

    &__schedule-item-time {
      padding: 0;
    }

    &__schedule-item-title {
      padding: 0;
    }

    &__schedule-item-button {
      width: 100%;
      margin: 32px 0 0;
      padding: 14px;
      border-radius: 8px;
      color: $blue;
      text-decoration: none;
      border: unset;

      &:hover {
        color: $blue;
        background-color: $white;
        border: unset;
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    margin: 0;

    &__list {
      margin: 32px 0 0;

      &--service {
        flex-direction: column;

        .content__item {
          margin: 0 auto 24px;

          img {
            display: block;
          }
        }
      }

      &--repair {
        .content__item {
          margin: 0 0 32px;

          &:nth-child(2n) {
            flex-direction: row;

            .content__item-description {
              margin: 0 0 0 24px;
            }
          }
        }

        .content__item-description {
          max-width: 100%;
        }
      }
    }

    &__item {
      img {
        display: none;
      }
    }

    &__schedule-list {
      @include list;
      margin: 32px 0 0;
      display: flex;
      flex-direction: column;
    }

    &__schedule-item {
      width: 100%;
      margin: 0 0 24px;

      &:nth-child(2n) {
        margin: 0 0 24px;
      }
    }

    &__schedule-item-date {
      text-align: left;
    }

    &__schedule-item-time {
      padding: 0;
    }

    &__schedule-item-title {
      padding: 0;
    }

    &__schedule-item-button {
      width: 200px;
      margin: 32px 0 0 auto;
    }
  }
}
