.page-main {
  width: 100%;
  margin: 0 auto;
  flex: 1 0 auto;

  &__wrapper {
    width: 100%;
    max-width: 1220px;
    box-sizing: border-box;
    padding: 0 20px;
    margin: 60px auto;
    display: flex;
    justify-content: flex-start;
  }
}

@media (max-width: $tablet-max-width) {
  .page-main {
    &__wrapper {
      width: 100%;
      max-width: $tablet-max-width;
      margin: 40px auto;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .page-main {
    &__wrapper {
      width: 100%;
      max-width: $mobile-max-width;
      margin: 20px auto;
      flex-direction: column;
    }
  }
}
